import React, {Component} from 'react';
import sampleAstros from '../constellation.json';

export default class VirtualSky extends Component {

  constructor() {
    super();
    this.state = {
      name: '/'
    }
  }

  componentDidMount() {
    this.$sky = $(this.sky);
    this.planetarium = $.virtualsky({
      id: 'starmap',
      projection: 'stereo',
      constellations: true,
      objects: sampleAstros,
      onClickObject: (o) => this.setName(o.target.name),
      objectTemplate: (onPress) => this.objectTemplate(onPress)
    });
  }

  objectTemplate(onPress) {
    return (
        <div>
          <button onClick={onPress}>버튼!!!</button>
        </div>
    )
  }

  setName(name) {
    this.setState({ name })
  }

  moveTo() {
    this.planetarium.panTo(56.8690917,24.1053111,3000);
  }

  render() {
    return (
        <div>
          <div ref={sky => this.sky = sky} id={"starmap"} style={styles.container}></div>
          <h1>{this.props.mapShow ?
            'True' : 'False'
          }</h1>
          <h3>{this.state.name}</h3>
          <button onClick={() => this.moveTo()}>이동</button>
        </div>
    );
  }

}

const styles = {
  container: {
    height: 400,
    width: '100%'
  }
};



